
riot.tag2('flip-book', '<yield></yield>', 'flip-book,[riot-tag="flip-book"]{perspective:1200px}@page{size:7in 9.25in;margin:27mm 16mm 27mm 16mm}@page :left{@top-left{content:\'\'}}@page blank :left{@top-left{content:normal}}@page :right{@top-right{content:string(header, first)}}@page :left{@bottom-left{content:counter(page)}}@page front-matter :left{@bottom-left{content:counter(page, lower-roman)}}', '', function(opts) {
var root = this.root;

this.on('mount', function() {

  var selector = '> section', pages;
  try {
    pages = root.querySelectorAll(':scope' + selector);
  } catch (error) {
    var id = root.id;
    root.id = 'ID_' + Date.now();
    pages = document.querySelectorAll('#' + root.id + selector);
    root.id = id;
  }
});

this.on('update', function() {

});
});